import React, { Component } from 'react';
import { Navbar, NavbarBrand,Nav, NavbarToggler, Collapse, NavItem, Jumbotron, Button, Modal, ModalHeader, ModalBody, 
    Form, FormGroup, Input, Label } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {

    constructor(props) {
        super(props);
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        this.state={
            isNavOpen:false,
            isModalOpen:false,
        }
    }

toggleNav() {
    this.setState(
        { isNavOpen: !this.state.isNavOpen }
    );
}

toggleModal() {
    this.setState(
        { isModalOpen: !this.state.isModalOpen }
    );
}

handleLogin(event){
    this.toggleModal();
    alert("Username: "+ this.username.value +" Password: "+this.password.value+" Remember: "+this.remember.checked);
    event.preventDefault();
}

    render() {
        return (
            <div>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}><strong>Login</strong></ModalHeader>
                    <ModalBody>                        
                            <Form onSubmit={this.handleLogin}>
                                <FormGroup>
                                    <Label htmlFor="username">Username</Label>
                                    <Input type="text" id="username" name="username" innerRef={(input) => this.username = input}></Input>
                                </FormGroup>
                                <FormGroup>
                                    <Label htmlFor="password">password</Label>
                                    <Input type="text" id="password" name="password" innerRef={(input) => this.password = input}></Input>
                                </FormGroup>
                                <FormGroup check>
                                    <Label check>
                                        <Input type="checkbox" name="remember" innerRef={(input) => this.remember = input}></Input>
                                        Remember me
                                    </Label>
                                </FormGroup>
                                <Button type="submit" value="submit" color="primary">Login</Button>
                            </Form>
                    </ModalBody>
                </Modal> 
                <Navbar dark expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav}/>
                        <NavbarBrand className="mr-auto" href="/">
                            <img src="assets/images/logo.png" height="30" width="41" alt="Ristorante Con Fusion" />
                        </NavbarBrand>
                        <Collapse  isOpen={this.state.isNavOpen} navbar toggle={this.toggleNav}>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/home">
                                        <span className="fa fa-home fa-lg"></span> Home
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/about"  toggle={this.toggleNav}>
                                        <span className="fa fa-info us fa-lg"></span> About us
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/menu">
                                        <span className="fa fa-list fa-lg"></span> Menu
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/contactus">
                                        <span className="fa fa-address-card fa-lg"></span> Contact Us
                                    </NavLink>
                                </NavItem>
                            </Nav>
                        </Collapse>
                        <Nav Navbar className="ml-auto">
                            <NavItem>
                                <Button outline onClick={this.toggleModal} className="nav-link">
                                    <span className="fa fa-sign-in text-white"> Login</span>
                                </Button>
                            </NavItem>
                        </Nav>
                    </div>
                </Navbar>

                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Ristorante con Fusion</h1>
                                <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
          </div>
      
        );
    }
}

export default Header;